* {
  box-sizing: border-box;

}
.nav {
  position: absolute;
  width: 100%;
  z-index: 99;
}

.nav ul {
  display: flex;
  align-items: center;
  justify-content: right;
  width: 100%;
  list-style: none;
  color: #353535;
  padding: 3rem 4rem;
  margin: 0px;

}

.nav ul li:first-child {
  margin-right: auto;
  margin-left: 0;
  font-size: 1rem;
}

.nav ul li:first-child span{
  color: #6b71e9;
}

.nav ul li{
  padding: 0;
  margin-left: 40px;
}

.hero {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  padding: 3rem 0rem 3rem 3rem;
  background-repeat: no-repeat;
  background-position: right top;
  background-size: 50%;

  background-color: #d9dffd;  /* fallback for old browsers */

}

.hero .text {
  z-index: 99;
  width: 40%;
}

.hero .text h1 {
  color: #6b71e9;
  font-size: 3rem;
}

.hero .text p {
  color: #636363;
}

.hero .text a:first-of-type {
  display: inline-block;
  width: fit-content;
  color: #fff;
  background-color: #6b71e9;
  padding: 12px 16px;
  border-radius: 20px;
  margin-top: 10px;
  margin-right: 20px;
  text-decoration: none;
  font-weight: 500;
}

.hero .text a {
  display: inline-block;
  width: fit-content;
  color: #6b71e9;
  padding: 12px 16px;
  border-radius: 20px;
  margin-top: 10px;
  margin-right: 20px;
  text-decoration: underline;
  font-weight: 500;
}

.hero .text div {
  display: inline-block;
  margin-top: 40px;
  width: fit-content;
}

.hero .text div span {
  color: #6b71e9;
  font-size: 2rem;
  font-weight: 600;
  margin-right: 100px;
}

.hero .spline-container {
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: flex-end;

}

.hero .spline-container .spline {
  width: 70%;
}

.user-items-list-simple .list-item-content__description p {
  font-size: 18px;
  }

  @media screen and (min-width: 1600px) {
  .user-items-list-simple[data-num-columns="6"] {
      grid-template-columns: repeat(11,1fr);
      grid-gap: 20px 10px;
  }

}

@media (max-width: 1080px) {
  .nav ul {
    padding: 2rem;
  }

  .hero {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
  
  .hero .text {
    z-index: 99;
    width: 100%;
    padding: 5rem 2rem 2rem 2rem;

  }
  .hero .spline-container {
    height: 100%;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    margin-top: 10%;
  }
  
  .hero .spline-container .spline {
    width: 100%;
  }
  
}

@media (max-width: 640px) {
  .nav ul {
    padding: 2rem;
  }

  .nav ul li:nth-child(2), .nav ul li:nth-child(3), .nav ul li:nth-child(4){
    display: none;
  }

  .hero {
    width: 100%;
    height: unset;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
  
  .hero .text {
    z-index: 99;
    width: 100%;
    padding: 5rem 2rem 2rem 2rem;

  }
  .hero .spline-container {
    height: 700px;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: -300px;
  }
  
  .hero .spline-container .spline {
    width: 100%;
  }
  
}